//mixins
@mixin text-template($size, $family,$color) {
font-size:$size;
font-family:$family;
//border-radius:.625rem;
color:$color;
//padding: 0px 2px 20px 2px !important;
} //end text-template

@mixin text($family,$size,$weight,$color) {
font-size:$size;
font-family:$family;
font-weight: $weight;
color:$color;
} //end text

@mixin text-r($family,$size,$weight,$color) {
font-size:$size * 0.8;
font-family:$family;
font-weight: $weight;
color:$color;

@include responsive(phone){
font-size:$size * 0.85 ;
}

@include responsive(tablet){
font-size:$size * 0.9 ;
}

@include responsive(laptop){
font-size: $size * 0.95;
}

@include responsive(desktop){
font-size:$size;//~21
}
} //end text





@mixin background($img, $color, $screen, $size ) {
background-image: linear-gradient(to right bottom, rgba($color, $screen), rgba($color, $screen)), url($img);
background-size: $size;
background-repeat:no-repeat;
background-position:center center;
} //end background mixin

@mixin background1($img, $color, $color-mix, $screen, $size) {
$color1: get-color($color, $color-mix);
background-image: linear-gradient(to right bottom, rgba($color1, $screen), rgba($color1, $screen)), url($img);
background-size: $size;
background-repeat:no-repeat;
background-position:center center;
} //end background mixin


@mixin background-gradient($degrees, $color, $color-mix, $per, $color-2, $color-mix-2, $screen, $size, $img ) {
$color1: get-color($color, $color-mix);
$color2: get-color($color, $color-mix-2);
background-image: linear-gradient($degrees, rgba($color1, $screen),$per, rgba($color2, $screen)), url($img);
background-size: $size;
background-repeat:no-repeat;
background-position:center center;
} //end background mixin

