.footer {
grid-column: col-start 1 / span 12;
grid-row:10;
display:grid;
grid-template-columns: 1fr 1fr 1fr;
grid-template-rows:.5fr 1fr .5fr;
min-height:150px;
width:100%;
background:$color-band;
align-items:center;
padding:$pad-small;

@include responsive(pixel-2){
grid-template-rows:.5fr 1fr .5fr .5fr;
}


@include text-template($large,$font-family-ads, $color-primary);
text-align:center;

& > .hugo{
grid-column:1/4;
grid-row:1;
height:auto;
width:100%;
margin:5px 0px;
text-align:center;
align-self:start;
}//end hugo


& > a.develop{
justify-self:center;
grid-column:2;
grid-row:2;
width:100px;
height:50px;
margin: 0px 10px 0px 10px;

@include responsive(phone){
margin: 10px;
height:50px;
width:100px;
}
@include responsive(big-tablet){
height:60px;
width:110px;
margin: 15px 10px 15px 10px;
}

& > .footer-logo{
background-size:cover;
background-position:center center;
background-repeat:no-repeat;
border-radius:$rad-normal;
height:100%;
width:100%;
transition: all .2s ease-in-out;
}//end footer logo
}//end develop

& > a.develop:hover{
transform: scale(1.2);
}

& > .copy{
grid-column:1/4;
grid-row:3;
margin:8px 0px;

@include responsive(pixel-2){
grid-row:4;
}
}

& > section{
display:flex;
flex-wrap:wrap;
align-content:center;
justify-content:center;

& > small{
height:auto;
width:100%;
//margin-bottom:15px;

& > a{
@include text-template($small,$font-family-ads, $color-silver-1);
text-decoration:underline;

@include responsive(tablet){
//font-size: $small;
}
}//a
}//end small
}//end sections


& > .footer-right{
grid-column:3;
grid-row:2;
align-items:center;

@include responsive(pixel-2){
grid-column:2;
grid-row:3;
text-align:center;
}



@include responsive(tablet){
text-align:left;
}

& > small > a:hover{
color: $green-screen;
font-size:102%;
}//end hover

}//end footer rigth
}//end footer
